import React, { Component } from 'react'

import { List, ListItem } from 'material-ui/List'
import Divider from 'material-ui/Divider'
import ActionGrade from 'material-ui/svg-icons/action/grade'
import ContentInbox from 'material-ui/svg-icons/content/inbox'
import ContentDrafts from 'material-ui/svg-icons/content/drafts'
import ContentSend from 'material-ui/svg-icons/content/send'
import Subheader from 'material-ui/Subheader'
import AppBar from 'material-ui/AppBar'
import IconButton from 'material-ui/IconButton'
import FontIcon from 'material-ui/FontIcon'
import Paper from 'material-ui/Paper'

import Markdown from 'react-remarkable'
import $ from 'jquery'
import scrollify from 'jquery-scrollify'

import IonIcon from './IonIcon'

import Background from './circle-mess.svg'

import './App.styl'
import './Markdown.styl'

const programm = `## Unser Programm hat Konzept

Das **Grundgerst** unseres Programmes bilden die Samstagprogramme. Diese finden in zweiwchtenlich am Samstagnachmittag an verschiedenen Orten in der Stadt Zrich statt. Der Zweiwochenrhythmus erlaubt eine unkomplizierte Planung des Familienprogramms. Zur Erreichung der Gruppenzugehrigkeit ist eine regelmssige Teilnahme an den bungen von grosser Bedeutung. Whrend der Schulferien finden keine Samstagsprogramme statt.


 [![](http://zuerich.kadetten.ch/wp-content/uploads/2011/03/programmkonzept.gif "programmkonzept")](http://zuerich.kadetten.ch/wp-content/uploads/2011/03/programmkonzept.gif)

Das Grundkonzept unseres Programms.

## Quartalsprogramm

Wir verffentlichen viermal jhrlich das **Quartalsprogramm**, welches Informationen ber die Daten und Orte der Samstagsprogramme sowie ber Lager und Spezialanlsse enthlt.

Hhepunkte des Kadettenprogrammes stellen die regelmssig durchgefhrten **Lager und Spezialanlsse** dar. Sie erlauben die Anwendung und Vertiefung des an den Samstagsprogrammen gelernten sowie die Festigung der Gruppenzugehrigkeit. Fr die Teilnahme an Lagern und Spezialanlsse werden separate Einladungen versandt.

Whrend der wrmeren Perioden bieten die Kadetten Zrich Zeltlager unterschiedlicher Dauer an (ber Pfingsten oder in den Sommerferien). Hauslager und Weekends werden z.B. in den Frhlings-, Herbst- oder Weihnachtsferien sowie ber das Chlauswochenende durchgefhrt.

Gerade die krzeren Lager (Pfingstlager, Chlausweekend) erlauben es auch Jungkadetten, erste Schritte weg von zu Hause zu unternehmen.

Verschiedene **Spezialanlsse** runden das Jahresprogramm ab. Dazu zhlen z.B. das Nachtspektakel (Abendbung), das Sportweekend, der Oberlnder Nachtmarsch, das Grmpelturnier, die Schweizerischen Kadettentage sowie das Ski- und Snberweekend.

Einzelne dieser Anlsse finden zusammen mit Kadetten aus anderen Orten statt.

`

export default class App extends Component {
	constructor(props) {
		super(props)
		this.state = {
			fetchedcontent: '',
			menustate: false,
			menuicon: 'menu',
			menu: [
				{ name: 'Aktuell', icon: 'whatshot' },
				{ name: 'Programm', icon: 'insert_invitation' },
				{
					name: 'Mitmachen',
					icon: 'person_add',
					submenu: ['Schnupperbesuch', 'Kosten + Leistungen', 'Anmelden + Mitmachen']
				},
				{ name: 'Gallerie', icon: 'photo_library' },
				{ name: 'ber uns', icon: 'help', submenu: ['Kontakt', 'Leiter + Trgerschaft', 'Ausbildung'] },
				{ devider: true },
				{ name: 'Leiter', icon: 'supervisor_account', submenu: ['Kaderbox'] }
			]
		}
	}

	componentWillMount = () => this.fetchContent()
	componentDidMount = () => {
		$.scrollify({
			section: '.Section',
			sectionName: 'section-name',
			interstitialSection: '',
			easing: 'easeOutExpo',
			scrollSpeed: 1100,
			offset: 0,
			scrollbars: true,
			standardScrollElements: '.Menu',
			setHeights: true,
			overflowScroll: true,
			updateHash: true,
			touchScroll: true,
			before: function() {
				$.scrollify.update()
			},
			after: function() {},
			afterResize: function() {},
			afterRender: function() {}
		})
	}
	fetchContent = () => {
		fetch('https://raw.githubusercontent.com/acdlite/react-remarkable/master/README.md')
			.then(response => {
				return response.text()
			})
			.then(fetchedcontent => this.setState({ fetchedcontent }))
	}

	toggleMenu = () => {
		let menuicon
		let menustate = !this.state.menustate
		if (menustate) {
			menuicon = 'close'
		} else {
			menuicon = 'menu'
		}
		this.setState({ menustate, menuicon })
	}
	getMenu = () =>
		this.state.menu.map(({ name, icon, submenu, devider }) => {
			if (devider) {
				return <Divider />
			} else if (typeof submenu === 'undefined') {
				return (
					<ListItem
						className="item"
						primaryText={name}
						leftIcon={<FontIcon className="material-icons">{icon}</FontIcon>}
					/>
				)
			} else {
				return (
					<ListItem
						className="item"
						primaryText={name}
						leftIcon={<FontIcon className="material-icons">{icon}</FontIcon>}
						primaryTogglesNestedList={true}
						nestedItems={submenu.map(menutext => <ListItem className="subitem" primaryText={menutext} />)}
					/>
				)
			}
		})

	render() {
		return (
			<div className={`App ${this.state.menuicon}`}>
				{programm.split('## ').map((section, index) => {
					if (index == 0) {
						return false
					} else {
						return (
							<div className="Section" data-section-name={section.split('\n')[0]}>
								<Paper className="Markdown" zDepth={5}>
									<Markdown source={'## ' + section} />
								</Paper>
							</div>
						)
					}
				})}
				<AppBar
					className="AppBar"
					title="Kadetten Zrich"
					iconElementRight={
						<span className="logo">
							<IonIcon icon="leaf" />
							<IonIcon icon="compass" />
							<IonIcon icon="bonfire" />
							<IonIcon icon="map" />
						</span>
					}
					iconElementLeft={
						<IconButton onClick={this.toggleMenu} iconClassName="material-icons">
							{this.state.menuicon}
						</IconButton>
					}
				/>
				<Paper zDepth={2} className={`Menu ${this.state.menuicon}`}>
					<List>{this.getMenu()}</List>
				</Paper>
				<Paper zDepth={1} className="Title">
					<h1>Programm</h1>
				</Paper>

				<div
					className="bg"
					style={{
						backgroundImage: `url(${Background})`
					}}
				/>
			</div>
		)
	}
}



